import React, { useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import Data from '../Data.js';
import { useTranslation } from 'react-i18next';
import 'swiper/css';
import 'swiper/css/pagination';
import { Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';

export default function App() {
  const [reviewData] = useState(Data[1]); // Data2
  const { t } = useTranslation();

  return (
    <Container className="mySwiper3">
      <h3>{t('review.title')}</h3>
      <p>{t('review.desc')}</p>
      <Swiper
        slidesPerView={2}
        spaceBetween={5}
        pagination={{ clickable: true }}
        breakpoints={{
          640: { slidesPerView: 2, spaceBetween: 20 },
          768: { slidesPerView: 3, spaceBetween: 40 },
          1024: { slidesPerView: 4, spaceBetween: 30 },
        }}
        modules={[Pagination]}
        className="mySwiper3"
      >
        {reviewData.map((product) => (
          <SwiperSlide key={product.id}>
            <div className="imgbox">
              <img
                src={process.env.PUBLIC_URL + product.imageSrc}
                alt={t(product.altText)} // 다국어 alt
              />
            </div>
            <div className="txtbox">
              <p>{t(product.content)}</p> {/* 다국어 content */}
            </div>
            <p className="ReIn">
              <span>{product.reviewid}</span> | &nbsp;
              <span>{product.date}</span>
            </p>
          </SwiperSlide>
        ))}
      </Swiper>
      <button>{t('review.writeButton')}</button>
    </Container>
  );
}
