import React, { useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import { EffectFade, Navigation, Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import '../App.scss';
import { useTranslation } from 'react-i18next';

export default function App() {
  const navigate = useNavigate();
  const swiperRef = useRef(null);
  const swiperContainerRef = useRef(null);
  const { t } = useTranslation();

  const animateActiveSlide = () => {
    const allSlides = document.querySelectorAll('.swiper-slide');
    allSlides.forEach(slide => {
      const bgDiv = slide.querySelector('.bg-animate');
      if (bgDiv) {
        bgDiv.style.animation = '';
        void bgDiv.offsetWidth;
        bgDiv.style.animation = 'bgSlideDownFade 1s ease-out forwards';
      }
    });

    const activeSlide = document.querySelector('.swiper-slide-active');
    if (activeSlide) {
      const bgDiv = activeSlide.querySelector('.bg-animate');
      if (bgDiv) {
        bgDiv.style.animation = 'bgSlideDownFade 1s ease-out forwards';
      }
    }
  };

  const textanimation = () => {
    const allSlides = document.querySelectorAll('.swiper-slide');
    allSlides.forEach(slide => {
      const txtbox = slide.querySelector('.txtbox');
      if (txtbox) {
        txtbox.querySelectorAll('h3, p, button').forEach(el => {
          el.classList.remove('animate');
        });
      }
    });

    const activeSlide = document.querySelector('.swiper-slide-active');
    if (activeSlide) {
      const txtbox = activeSlide.querySelector('.txtbox');
      if (txtbox) {
        txtbox.querySelectorAll('h3, p, button').forEach(el => {
          const _ = el.offsetWidth;
          el.classList.add('animate');
        });
      }
    }
  };

  useEffect(() => {
    if (!swiperContainerRef.current) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          animateActiveSlide();
          textanimation();
        }
      },
      { threshold: 0.5 }
    );

    observer.observe(swiperContainerRef.current);

    return () => {
      observer.disconnect();
    };
  }, []);

  const onInitHandler = () => {
    animateActiveSlide();
    textanimation();
  };

  return (
    <div ref={swiperContainerRef}>
      <Swiper
        spaceBetween={30}
        effect="fade"
        navigation={true}
        pagination={{ clickable: true }}
        modules={[EffectFade, Navigation, Pagination]}
        loop={true}
        className="mySwiper mainslider"
        onSlideChangeTransitionEnd={textanimation}
        onSlideChange={animateActiveSlide}
        onInit={onInitHandler}
        ref={swiperRef}
      >
        <SwiperSlide>
          <div
            className="bg-animate"
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback1.png)`,
              backgroundColor: 'white',
            }}
          />
          <Container>
            <div className="txtbox">
              <h3>{t('home.title1')}</h3>
              <p>{t('home.desc1')}</p>
              <button onClick={() => navigate('/whole')}>{t('home.buyBtn')}</button>
            </div>
          </Container>
        </SwiperSlide>

        <SwiperSlide>
          <div
            className="bg-animate"
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback2.jpg)`,
              backgroundColor: 'white',
            }}
          />
          <Container>
            <div className="txtbox">
              <h3 style={{ color: 'white' }}>{t('home.title2')}</h3>
              <p style={{ color: 'white' }}>{t('home.desc2')}</p>
              <button
                style={{ backgroundColor: '#ea462b' }}
                onClick={() => navigate('/whole')}
              >
                {t('home.buyBtn')}
              </button>
            </div>
          </Container>
        </SwiperSlide>

        <SwiperSlide>
          <div
            className="bg-animate"
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback3.png)`,
              backgroundColor: 'white',
            }}
          />
          <Container>
            <div className="txtbox">
              <h3>{t('home.title3')}</h3>
              <p>{t('home.desc3')}</p>
              <button onClick={() => navigate('/whole')}>{t('home.buyBtn')}</button>
            </div>
          </Container>
        </SwiperSlide>

        <SwiperSlide>
          <div
            className="bg-animate"
            style={{
              backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback4.png)`,
              backgroundColor: 'white',
            }}
          />
          <Container>
            <div className="txtbox">
              <h3>{t('home.title4')}</h3>
              <p>{t('home.desc4')}</p>
              <button>{t('home.inquireBtn')}</button>
            </div>
          </Container>
        </SwiperSlide>
      </Swiper>
    </div>
  );
}
