import React, { useState } from 'react';
import { Container, Row, Col, Form, InputGroup, Button, Collapse } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

import { useTranslation } from 'react-i18next';

const Whole = ({ products }) => {
  const navigate = useNavigate();

  // 상태 정의
  const [searchKeyword, setSearchKeyword] = useState('');
  const [sortOrder, setSortOrder] = useState('');
  const [selectedFoods, setselectedFoods] = useState([]);
  const [selectedServings, setSelectedServings] = useState([]);
  const [showFilters, setShowFilters] = useState(false); // 🔄 필터 토글 상태

  // 필터 초기화 함수
  const resetAllFilters = () => {
    setSearchKeyword('');
    setSortOrder('');
    setselectedFoods([]);
    setSelectedServings([]);
  };

  // 체크박스 토글 핸들러
  const toggleSelection = (value, setFn, selectedList) => {
    if (selectedList.includes(value)) {
      setFn(selectedList.filter((v) => v !== value));
    } else {
      setFn([...selectedList, value]);
    }
  };

  // 필터링된 상품 리스트
  const filteredProducts = products
    .filter(product => product.title.toLowerCase().includes(searchKeyword.toLowerCase()))
    .filter(product => {
      if (selectedFoods.length > 0 && !selectedFoods.includes(product.typesoffood)) return false;

      if (selectedServings.length > 0) {
        const s = product.serving;
        const isMatch = selectedServings.some(range => {
          if (range === '1') return s === 1;
          if (range === '2-3') return s >= 2 && s <= 3;
          if (range === '4+') return s >= 4;
          return false;
        });
        if (!isMatch) return false;
      }

      return true;
    })
    .sort((a, b) => {
      if (sortOrder === 'asc') return a.price - b.price;
      if (sortOrder === 'desc') return b.price - a.price;
      return 0;
    });
  const { t } = useTranslation();
  return (
    <Container className="whole">
      <h3>전체제품</h3>
      <p>박쉐프의 전체 제품을 소개합니다</p>

      {/* 검색창 */}
      <InputGroup className="mb-3">
        <Form.Control
        
          placeholder="상품명을 입력하세요"
          value={searchKeyword}
          onChange={(e) => setSearchKeyword(e.target.value)}
        />
      </InputGroup>

      {/*  필터 토글 버튼 */}
      <div className="text-end mb-2 fitterBtn">
        <button
          
          onClick={() => setShowFilters(!showFilters)}
          aria-controls="filter-collapse"
          aria-expanded={showFilters}
        >
          {showFilters ? '필터 숨기기' : '필터 보기'}
        </button>
             <button
          onClick={resetAllFilters}
        >
          전체 초기화
        </button>
      </div>

      {/* 필터 영역 */}
      <Collapse in={showFilters}>
        <div id="filter-collapse">
          <Row className="mb-3" xs={2}>
            <Col md={6}>
              <Form.Check
                label="돼지고기 (pork)"
                value="pork"
                checked={selectedFoods.includes('pork')}
                onChange={() => toggleSelection('pork', setselectedFoods, selectedFoods)}
              />
              <Form.Check
                label="소고기 (beef)"
                value="beef"
                checked={selectedFoods.includes('beef')}
                onChange={() => toggleSelection('beef', setselectedFoods, selectedFoods)}
              />
            </Col>
            <Col md={6}>
              <Form.Check
                label="1인분 (1 serving)"
                value="1"
                checked={selectedServings.includes('1')}
                onChange={() => toggleSelection('1', setSelectedServings, selectedServings)}
              />
              <Form.Check
                label="2~3인분 (2–3 servings)"
                value="2-3"
                checked={selectedServings.includes('2-3')}
                onChange={() => toggleSelection('2-3', setSelectedServings, selectedServings)}
              />
              <Form.Check
                label="4인분 이상 (4+ servings)"
                value="4+"
                checked={selectedServings.includes('4+')}
                onChange={() => toggleSelection('4+', setSelectedServings, selectedServings)}
              />
            </Col>
          </Row>
        </div>
      </Collapse>

      {/* 정렬 + 초기화 */}
      <div className="d-flex justify-content-end">
        <Form.Select
          className="mb-2 Sortby "
          style={{ width: '200px', border: '1px solid #ea462b' }}
          value={sortOrder}
          onChange={(e) => setSortOrder(e.target.value)}
        >
          <option value="">-- 정렬 선택 --</option>
          <option value="asc">최저가순</option>
          <option value="desc">최고가순</option>
        </Form.Select>

   
      </div>

      {/* 상품 출력 */}
      <Row>
        {filteredProducts.length > 0 ? (
          filteredProducts.map((product) => (
            <Col className="mb-3 productbox" md={4} sm={6} xs={12} key={product.id} onClick={() => navigate('/detail/' + product.id)}>
              <div className="imgbox">
                <img src={process.env.PUBLIC_URL + product.imageSrc} alt={product.altText} />
              </div>
              <div className="txtbox">
                  <h5>{t(product.title)}</h5>
                  <p>{t(product.description)}</p>
                <p>{product.price.toLocaleString()}원</p>
                <button className="addbtn">상품추가</button>
              </div>
            </Col>
          ))
        ) : (
          <p>검색 결과가 없습니다.</p>
        )}
      </Row>
    </Container>
  );
};

export default Whole;
