import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/pagination';

// import required modules
import { Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

import { useTranslation } from 'react-i18next';

export default function Secondslider(props) {

  const { best } = props
  const navigate = useNavigate();
  const { t } = useTranslation();

  return (
    <div className="SCC pt-5">
      <Container>
        <h3>{t('slider.sectionTitle')}</h3>
        <p>{t('slider.sectionDesc')}</p>
        <Swiper
          slidesPerView={1}
          spaceBetween={0}
          pagination={{ clickable: true }}
          breakpoints={{
            640: {
              slidesPerView: 2,
              spaceBetween: 20,
            },
            768: {
              slidesPerView: 2,
              spaceBetween: 40,
            },
            1024: {
              slidesPerView: 3,
              spaceBetween: 30,
            },
          }}
          modules={[Pagination]}
          className="mySwiper2"
        >

          {
            best.map((product, index) =>
              <SwiperSlide key={index} onClick={() => { navigate('/detail/' + product.id) }}>
                <div className="imgbox">
                  <img src={process.env.PUBLIC_URL + product.imageSrc} alt={product.altText || product.title} />
                </div>
                <div className="txtbox">
                  <h5>{t(product.title)}</h5>
                  <p>{t(product.description)}</p>
                  
                </div>
              </SwiperSlide>
            )
          }
        </Swiper>
      </Container>
    </div>
  );

}