import React from 'react';
import { Button, Container, Table, Row, Col } from 'react-bootstrap';
import { useDispatch, useSelector } from "react-redux";
import { addCount, sortName, decreaseCount } from "./../store.js";
import { useTranslation } from 'react-i18next';

function Cart() {
   const user = useSelector(state => state.user);
   const cart = useSelector(state => state.cart);
   const { name, age } = user;
   const dispatch = useDispatch();

   // 총 결제금액 계산
   const total = cart.reduce((acc, item) => acc + item.price * item.amount, 0);
const { t } = useTranslation();
   return (
      <Container style={{ paddingTop: '100px', paddingBottom: '10%' }} className='Cart'>
         <h5>{name} {age} (님)의 장바구니</h5>

         <Table>
            <thead>
               <tr>
                  <th className='Cartremove'>상품</th>
                  <th className='Cartremove'>수량</th>
                  <th className='Cartremove'>낱개 가격</th>
                  <th className='Cartremove1'>총가격</th>
                  <th className='Cartremove'>변경하기</th>
               </tr>
            </thead>
            <tbody>
               {
                  cart.map((v, i) => (
                     <tr key={i}>
                        <td className="tdimg">
                           <Row className="align-items-center">
                              <Col xs={12} className="text-center mb-2">
                                 <img
                                    src={process.env.PUBLIC_URL + v.imgUrl}
                                    alt={t(v.item)}
                                    style={{ width: '100px' }}
                                 />
                              </Col>
                              <Col xs={12} className="text-center">
                                {t(v.item)}
                              </Col>
                           </Row>
                        </td>
                        <td>{v.amount}개</td>
                        <td>{v.price.toLocaleString()}원</td>
                        <td className='Cartremove1'>{(v.price * v.amount).toLocaleString()}원</td>
                        <td className='amoutBTN'>
                           <button onClick={() => dispatch(addCount(i))}>
                              <span className="material-symbols-outlined">add</span>
                           </button>
                           <button onClick={() => dispatch(decreaseCount(i))}>
                              <span className="material-symbols-outlined">remove</span>
                           </button>
                        </td>
                     </tr>
                  ))
               }
            </tbody>
         </Table>

         <div style={{ textAlign: 'right', marginTop: '20px' }}>
            <h4>총 결제금액: {total.toLocaleString()}원</h4>
           <button onClick={() => alert('로그인을 해주세요')}>결제하러가기</button>
         </div>
      </Container>
   );
}

export default Cart;
