import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { useNavigate } from 'react-router-dom';
import i18n from '../i18n';
import { useTranslation } from 'react-i18next';




function Header() {
  const navigate = useNavigate();
  const { t } = useTranslation();
  return (
    <header>
      <Navbar collapseOnSelect expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/') }}><img src={process.env.PUBLIC_URL + '/img/logo.png'} alt="헤더로고" /></Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate('/Whole') }}>{t("전체보기")}</Nav.Link>
              <Nav.Link href="#pricing">{t("선물세트")}</Nav.Link>
              <NavDropdown title={t("커뮤니티")} id="collapsible-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">{t("공지사항")}</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">
                  {t("레시피")}
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">{t("문의사항")}</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  {t("회사소개")}
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
            <Nav>
              <Nav.Link onClick={() => { navigate('/cart') }} >{t("장바구니")}</Nav.Link>
              <Nav.Link >
                {t("마이페이지")}
              </Nav.Link>
           

            </Nav>
          </Navbar.Collapse>
        </Container>   <button className="ChangeLang" onClick={() => i18n.changeLanguage('en')}>English</button>
              <button className="ChangeLang"onClick={() => i18n.changeLanguage('ko')}>한국어</button>
      </Navbar>
    </header>
  );
}



export default Header;
