
import { Container } from 'react-bootstrap'
import { useTranslation } from 'react-i18next';

const Banner = () => {
  const { t } = useTranslation();
  return (
    <div className='banner1' style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback5.png)` }}>
      <Container sm={6}>
        <div className="txtbox">
          <h5>{t('banner.promotion')}</h5>
          <h4>
            {t('banner.challengeLine1')}<br />
            {t('banner.challengeLine2')}
          </h4>
          <h3>{t('banner.spicyBulgogi')}</h3>
          <button onClick={() => alert(t('banner.addedToCartAlert'))} className="mt-5">
            {t('banner.addToCart')}
          </button>
        </div>
      </Container>
    </div>
  )
}

export default Banner;
