import React from 'react';
import { Row, Col, Container } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';
import i18n from '../i18n';

function Footer() {
  const { t } = useTranslation();

  return (
    <Container className='footer'>
      <Row>
        <Col md={4}>
          <div className="logbox">
            <img src={process.env.PUBLIC_URL + '/img/logo.png'} alt="푸터로고" />
          </div>
          <div className="txtbox">
            <p>
              {t('footer.description.line1')}<br />
              {t('footer.description.line2')}<br />
              {t('footer.description.line3')}
            </p>
               <button className="ChangeLangF" onClick={() => i18n.changeLanguage('en')}>English</button>
              <button className="ChangeLangF"onClick={() => i18n.changeLanguage('ko')}>한국어</button>
          </div>
        </Col>

        <Col md={4} className='contact'>
          <h4>{t('footer.contactTitle')}</h4>
          <div>
            <p style={{ display: "inline-flex" }}>
              <span className="material-symbols-outlined">home_pin</span>
              {t('footer.address')}
            </p>
          </div>
          <div>
            <p style={{ display: "inline-flex" }}>
              <span className="material-symbols-outlined">phone_iphone</span>
              {t('footer.phone')}
            </p>
          </div>
                     <p style={{ display: "inline-flex" }}> 
            <span className="material-symbols-outlined">
mail
</span>
            711psb@naver.com
            </p>

          <nav className="snsbox">
            <span><img src={process.env.PUBLIC_URL + '/img/kakao.png'} alt="카카오" /></span>
            <span><img src={process.env.PUBLIC_URL + '/img/insta.png'} alt="인스타그램" /></span>
            <span><img src={process.env.PUBLIC_URL + '/img/naver.png'} alt="네이버" /></span>
          </nav>
        </Col>

        <Col md={4}>
          <h4>{t('footer.infoTitle')}</h4>
          <div className="copyrightbox">
            <p>{t('footer.corpInfo')}</p>
            <p>
              <span>{t('footer.terms')}</span>
              <span>{t('footer.privacy')}</span>
              <span>{t('footer.guide')}</span>
            </p>
            <p>Copyright © 2020 Koad Bistro. All rights reserved.</p>
          </div>
        </Col>
      </Row>
    </Container>
  );
}

export default Footer;
