
import { Routes, Route, useNavigate, Outlet, Link } from 'react-router-dom';
import './App.scss';
import Banner from './components/Banner';
import Banner2 from './components/Banner2';
import Footer from './components/Footer';
import Header from './components/Header';
import Mainslider from './components/Mainslider';
import Secondslider from './components/Secondslider';
import Secondslider2 from './components/Secondslider2';
import Thirdslider from './components/Thirdslider';
import Detali from './components/Detali';
import Data from './Data';
import { useState } from 'react';
import Cart from './components/Cart';
import Whole from './components/Whole';


function App() {
  const [products] = useState(Data[0]);
const best = products.filter(product => product.category.includes('best'));
const choice = products.filter(product => product.category.includes('choice'));

  console.log(products)
  return (
    <div id='wrap'>
      <Header></Header>
      <Routes>
        <Route path="/" element={
          <>
            <Mainslider />
            <Secondslider best={best} />
            <Banner />
            <Secondslider2 choice={choice} />
            <Banner2 />
            <Thirdslider />
          </>}
        />
        <Route path="/detail/:id" element={<Detali products={products} />} />

        <Route path="/cart" element={<Cart />} />
        <Route path="/whole" element={<Whole products={products} />} />

      </Routes>
      <Footer></Footer>
    </div>

  );
}



export default App;
